

//------- boilerplate import ----

 import React from 'react';
 import { Link } from 'react-router-dom';

 //------- boilerplate import ----

 //------- function imported for navbar -----

function Navbar() {
    return (

        //Ordered List to contain tabs
        <ol className='nav nav-tabs'>

            {/* ---link for home--- */}
            <li className='nav-item'>
                <Link to='/' className={window.location.pathname === '/' ? 'nav-link active' : 'nav-link'}>
                    Home
                </Link>
            </li>

            {/* ---link for skills--- */}
            <li className='nav-item'>
                <Link to='/skills' className={window.location.pathname === '/' ? 'nav-link active' : 'nav-link'}>
                    Skills
                </Link>
            </li>

            {/* ---portfolio--- */}
            <li className='nav-item'>
                <Link to='/portfolio' className={window.location.pathname === '/' ? 'nav-link active' : 'nav-link'}>
                    Portfolio
                </Link>
            </li>

            {/* ---Me--- */}
            <li className='nav-item'>
                <Link to='/me' className={window.location.pathname === '/' ? 'nav-link active' : 'nav-link'}>
                    Me
                </Link>
            </li>

            {/* ---Contact--- */}
            <li className='nav-item'>
                <Link to='/contact' className={window.location.pathname === '/' ? 'nav-link active' : 'nav-link'}>
                    Contact
                </Link>
            </li>
        </ol>
    );
}

//------- function imported for navbar -----

 
//------- exporting function -----

export default Navbar;

//------- exporting function -----

 